8BAAA,sBACA,oBACA,cACA,kBACA,cACA,WAAY;CAjDxB;AAAA,2DAoDc,aC7gCK;CDy9BnB;AAAA,0BA0DI,kBACA,SACA,WACA,kBACA,2FAAA,4DAAsE;CA9D1E;AAAA,+BAgEM,WACA,kBACA,kBACA,oBAAA,oBAAA,aACA,yBAAA,sBAAA,mBACA,qBAAA,iBACA,yBAAA,sBAAA,8BACA,8BAAA,sBACA,qBAAiB;CAxEvB;AAAA,yCA0EQ,UACA,kBACA,uBACA,2BACA,YACA,WACA,uBAAmB;CAhF3B;AAAA,yCAmFQ,kBACA,SACA,UACA,YACA,SACA,oBAAA,oBAAA,aACA,yBAAA,sBAAA,mBACA,wBAAA,qBAAA,uBACA,kBACA,YACA,sBACA,WACA,oBACA,gBACA,6DAAA,oDC1jCW;CD2jCZ;AAAA,gBAqBL,WACA,YACA,gBACA,oBAAA,oBAAA,aACA,4BAAA,6BAAA,0BAAA,qBAAsB;CALxB;AAAA,yBAOI,sBACA,kBACA,qBACA,cACA,WAAY;CAXhB;AAAA,2BAcI,qBACA,kBACA,oBACA,gBACA,aAAc;CAlBlB;AAAA,2BAqBI,qBACA,kBACA,cACA,UAAW;CAxBf;AAAA,6BA2BI,UACA,YACA,aAAe;CA7BnB;AAAA,kCA+BM,uBACA,SAAU;CAhChB;AAAA,8BAoCI,UACA,kBACA,eACA,iBACA,QACA,mCACA,6FAAA,2DACA,YACA,UC5nCa;CCkCjB;AAAA,YACE,sBACA,0DAAA,kDACA,WACA,YACA,kBACA,mCAAA,2BACA,yBAAA,iBACA,8BAAA,qBAAsB;CACvB;AAAA,wCAGC,UACA,mCAAA,0BAA2B;CAC5B;AAAA,wCAGC,UACA,oCAAA,2BAA4B;CAC7B;AAAA,sGAMC,wBAAA,gBACA,kBACA,KAAM;CACP;AAAA,UAGC,SACA,UACA,YACA,WACA,kBACA,kCACA,iBAAkB;CARpB;AAAA,cAUI,oBAAqB;CAVzB;AAAA,cAaI,SACA,SAAU;CAdd;AAAA,oBAiBI,mDACA,mCACA,kCACA,YACA,iBAAkB;CAKnB;AAAA;AA1BH,wEAyCU,cAAe;CAzCzB;AAAA,oFA2CY,oBAAqB;CA3CjC;AAAA,sFA8CY,oBAAqB;CA9CjC;AAAA,oEAoDU,yBAA0B;CAC3B;CAAA","file":"App.vue","sourcesContent":["@import \"theme.scss\";\r\n/* 返回按钮*/\r\n.link{\r\n  width: 100%;\r\n  height: 80px;\r\n  display: flex;\r\n  align-items: center;\r\n  box-sizing: border-box;\r\n  padding: 0px 15px;\r\n  color: $blueColor;\r\n  .fanhui{\r\n    font-size: 35px;\r\n    margin-right: 15px;\r\n    font-weight: 600;\r\n  }\r\n}\r\n/*vant轻提示全局样式 */\r\n.van-toast {\r\n  min-width: 250px;\r\n  line-height: 50px;\r\n  font-size: 30px;\r\n  .van-toast__icon {\r\n    font-size: 100px;\r\n  }\r\n}\r\n/* 全局隐藏滚动条 */\r\n.sell::-webkit-scrollbar, \r\n.index::-webkit-scrollbar,\r\n.businessadmin::-webkit-scrollbar,\r\n.accountadmin::-webkit-scrollbar,\r\n.sell_list::-webkit-scrollbar,\r\n.roll::-webkit-scrollbar{\r\n  display: none;\r\n}\r\n/* 全局无数据背景样式 */\r\n.list-null {\r\n  width: 100%;\r\n  position: absolute;\r\n  top: 25%;\r\n  .conter {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    i {\r\n      width: 100%;\r\n      font-size: 400px;\r\n      color: $lightGrayColor;\r\n      text-align: center;\r\n    }\r\n    p {\r\n      color: $fontGreyColor;\r\n      text-align: center;\r\n      margin-top: 15px;\r\n    }\r\n  }\r\n}\r\n/*底部全局按钮样式 */\r\n.binding-but {\r\n  width: 100%;\r\n  height: 110px;\r\n  position: fixed;\r\n  bottom: 0;\r\n  left: 0;\r\n  align-items: center;\r\n  display: flex;\r\n  flex-wrap: nowrap;\r\n  background-color: #fff;\r\n  box-sizing: border-box;\r\n  padding: 0px 20px;\r\n  button {\r\n    width: 50%;\r\n    height: 90px;\r\n    border: none;\r\n    color: $whiteColor;\r\n    font-size: 35px;\r\n    position: relative;\r\n  }\r\n  span {\r\n    width: 2px;\r\n    height: 50px !important;\r\n    background-color: $whiteColor;\r\n    position: absolute;\r\n    z-index: 9999;\r\n    left: 50%;\r\n  }\r\n  .left-jiao {\r\n    background-image: linear-gradient(to left, $skyBlueColor, $blueColor);\r\n    border-radius: 50px 0px 0px 50px !important;\r\n  }\r\n  .right-jiao {\r\n    background-image: linear-gradient(to right, $skyBlueColor, $blueColor);\r\n    border-radius: 0px 50px 50px 0px !important;\r\n  }\r\n}\r\n/* 登录 */\r\n.login{\r\n  width: 100%;\r\n  height: 100%;\r\n  overflow: hidden;\r\n  position: relative;\r\n  .bgimg{\r\n    width: 100%;\r\n    height: 100%;\r\n   position: absolute;\r\n   top: 0;\r\n   left: 0;\r\n   z-index: -9999;\r\n  }\r\n  .top{\r\n    width: 100%;\r\n    height: 30%;\r\n  }\r\n  .head{\r\n    width: 75%;\r\n    height: 600px;\r\n    margin: 30px auto auto auto;\r\n    background-color: $whiteColor;\r\n    box-sizing: border-box;\r\n    padding: 30px;\r\n    box-shadow: 0px 5px 15px 2px $blueColor;\r\n    border-radius: 15px;\r\n    position: relative;\r\n    .margin-top{\r\n       margin-top: 0px !important;\r\n    }\r\n    .input{\r\n      height: 100px;\r\n      display: flex;\r\n      flex-wrap: nowrap;\r\n      align-items: center;\r\n      margin-top: 35px;\r\n      border-bottom: 1px solid $lightGrayColor;\r\n      .van-cell{\r\n        font-size: 30px;\r\n      }\r\n      .van-field__clear{\r\n        font-size: 30px;\r\n      }\r\n      label{\r\n        font-size: 50px;\r\n        color: $blueColor;\r\n      }\r\n    }\r\n    button{\r\n      width:100%;\r\n      height: 80px;\r\n      color: $whiteColor;\r\n      margin-top: 120px;\r\n      // position: absolute;\r\n      // left: 5%;\r\n      // bottom: 50px;\r\n      border: none;\r\n      border-radius: 50px;\r\n      background-image: linear-gradient(to left, $skyBlueColor, $blueColor);\r\n    }\r\n  }\r\n}\r\n\r\n/* 首页 */\r\n.index {\r\n  // overflow: hidden;\r\n  width: 100%;\r\n  height: 100%;\r\n  overflow-x: hidden;\r\n  overflow-y: auto;\r\n  overflow: auto;\r\n  .haed-notice {\r\n    .van-notice-bar {\r\n      height: 80px !important;\r\n      font-size: 35px !important;\r\n    }\r\n    .van-swipe-item.van-swipe-item {\r\n      overflow: hidden;\r\n      white-space: nowrap;\r\n      text-overflow: ellipsis;\r\n    }\r\n    .van-swipe__indicator {\r\n      background-color: $blueColor;\r\n    }\r\n    .van-icon {\r\n      font-size: 35px !important;\r\n      margin-right: 15px;\r\n    }\r\n    .van-ellipsis {\r\n      height: 80px !important;\r\n      line-height: 80px !important;\r\n    }\r\n  }\r\n  .notice-overlay {\r\n    .wrapper {\r\n      background-color: $whiteColor;\r\n      width: 80%;\r\n      margin: 50% auto auto auto;\r\n      border-radius: 15px;\r\n      box-sizing: border-box;\r\n      padding-bottom: 40px;\r\n      h3 {\r\n        text-align: center;\r\n        height: 90px;\r\n        line-height: 90px;\r\n        background-image: linear-gradient(to left, $skyBlueColor, $blueColor);\r\n        border-radius: 15px 15px 0px 0px;\r\n        color: $whiteColor;\r\n        margin: 0px 0px 40px 0px;\r\n      }\r\n      p {\r\n        margin: 0;\r\n        padding: 0px 20px;\r\n        text-align: left;\r\n        text-align: justify;\r\n      }\r\n      button {\r\n        width: 50%;\r\n        height: 70px;\r\n        background-image: linear-gradient(to left, $skyBlueColor, $blueColor);\r\n        border-radius: 50px;\r\n        border: none;\r\n        color: $whiteColor;\r\n        margin: 40px auto auto auto;\r\n        display: block;\r\n      }\r\n      .van-cell {\r\n        font-size: 30px !important;\r\n        line-height: 75px !important;\r\n      }\r\n      .van-field__clear {\r\n        font-size: 30px !important;\r\n      }\r\n    }\r\n  }\r\n  .my-swipe {\r\n    width: 90%;\r\n    height: 380px;\r\n    margin: 30px auto auto auto;\r\n    border-radius: 15px;\r\n    box-shadow: 0px 1px 15px 10px $lightGrayColor;\r\n    img {\r\n      width: 100%;\r\n      height: 100%;\r\n      border-radius: 15px;\r\n    }\r\n    .van-swipe__indicators {\r\n      bottom: 40px !important;\r\n    }\r\n    .van-swipe__indicator {\r\n      width: 30px !important;\r\n      height: 30px !important;\r\n    }\r\n  }\r\n  .list {\r\n    width: 90%;\r\n    margin:30px auto 30px auto;\r\n    background-color: $whiteColor;\r\n    box-sizing: border-box;\r\n    padding: 30px;\r\n    border-radius: 15px;\r\n    box-shadow: 0px 1px 15px 6px $lightGrayColor;\r\n    ul li{\r\n      height: 100px;\r\n      display: flex;\r\n      align-items: center;\r\n      justify-content: space-between;\r\n      .font-i{\r\n        font-size: 40px;\r\n      }\r\n      .icon-gexinghuashezhi{\r\n        color: $doderBlueColor;\r\n        font-size: 36px;\r\n      }\r\n      span{\r\n        width: 80%;\r\n        color: $greyColor;\r\n      }\r\n      .icon-gengduo1{font-size: 35px; color: $fontGreyColor;}\r\n      .icon-left-yingyeguanli{color: $orangeColor;}\r\n      .icon-xiaofeizhedongcha{color: $cyanColor;}\r\n      .icon-zhanghuguanli{color: $