 space-between;\r\n      li{\r\n        box-shadow: 0px 1px 15px 6px $lightGrayColor;\r\n        width: 45%;\r\n        height: 287px;\r\n        text-align: center;\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: center;\r\n        flex-direction: column;\r\n        box-sizing: border-box;\r\n        margin-bottom: 55px;\r\n        border-radius: 15px;\r\n        .link-router{\r\n         width: 100%;\r\n         display: flex;\r\n        align-items: center;\r\n        justify-content: center;\r\n        flex-direction: column;\r\n        }\r\n        span{\r\n          margin-top: 10px;\r\n          font-size: 35px;\r\n        }\r\n        .iconfont{\r\n          font-size: 80px;\r\n        }\r\n        .link-router01{\r\n          color: $doderBlueColor;\r\n        }\r\n        .link-router02{\r\n          color: $orangeColor;\r\n        }\r\n        .link-router03{\r\n          color: $cyanColor;\r\n        }\r\n        .link-router04{\r\n          color: $doderBlueColor;\r\n        }\r\n        .link-router05{\r\n          color: $blueColor;\r\n        }\r\n        .link-router06{\r\n          color: $pinkColor;\r\n        }\r\n        .link-router07{\r\n          color: $skyBlueColor;\r\n        }\r\n        .link-router08{\r\n          color: $doderBlueColor;\r\n        }\r\n      }\r\n    }\r\n    // ul {\r\n    //   width: 95%;\r\n    //   margin: 25px auto auto auto;\r\n    //   display: flex;\r\n    //   flex-wrap: wrap;\r\n    //   align-items: center;\r\n    //   box-sizing: border-box;\r\n    //   .router {\r\n    //     width: 25%;\r\n    //     margin: 29.5px;\r\n    //     text-align: center;\r\n    //     color: $greyColor;\r\n    //     .icon {\r\n    //       width: 100%;\r\n    //       height: 180px;\r\n    //       border-radius: 15px;\r\n    //     }\r\n    //     .icon-blue {\r\n    //       background-color: $doderBlueColor;\r\n    //     }\r\n    //     .icon-pink {\r\n    //       background-color: $