ign-items: center;\r\n    // border-bottom: 1px solid $lightGreyColor;\r\n    box-sizing: border-box;\r\n    padding: 0 15px;\r\n    background: linear-gradient(left, $blueColor, $skyBlueColor);\r\n    color: $whiteColor;\r\n    .time {\r\n      width: 62%;\r\n      // color: $fontGreyColor;\r\n      margin-right: 15px;\r\n      text-align: left;\r\n      white-space: nowrap;\r\n      overflow: hidden;\r\n    }\r\n    .total {\r\n      width: 38%;\r\n      text-align: center;\r\n      text-align: right;\r\n      white-space: nowrap;\r\n      overflow: hidden;\r\n    }\r\n  }\r\n  // .sell_list::-webkit-scrollbar {\r\n  //   display: none;\r\n  // }\r\n  .sell_list {\r\n    width: 95%;\r\n    height: 100%;\r\n    overflow: auto;\r\n    margin: auto;\r\n    .sell {\r\n      width: 100%;\r\n      // margin-top: 30px;\r\n      padding-bottom: 100px;\r\n      li {\r\n        width: 100%;\r\n        display: flex;\r\n        flex-wrap: nowrap;\r\n        justify-content: space-between;\r\n        text-align: center;\r\n        font-size: 32px;\r\n        border-bottom: 1px solid $lightGreyColor;\r\n        // background: linear-gradient(left, $pinkColor, $orangeColor);\r\n        // margin-bottom: 30px;\r\n        // color: $whiteColor;\r\n        // border-radius: 15px;\r\n        // box-shadow: 4px 5px 6px 5px rgba($color: $fontGreyColor, $alpha: 0.7);\r\n        // box-sizing: border-box;\r\n        // padding: 0 15px;\r\n        span {\r\n          width: 25%;\r\n          height: 100px;\r\n          overflow: hidden;\r\n          line-height: 100px;\r\n        }\r\n        .list_width {\r\n          width: 48% !important;\r\n          color: $fontGreyColor;\r\n        }\r\n        .brandName {\r\n          overflow: hidden;\r\n          white-space: nowrap;\r\n          text-overflow: ellipsis;\r\n          text-align: center;\r\n        }\r\n        .total_color {\r\n          color: $orangeColor;\r\n          text-align: center;\r\n          font-weight: 700;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n/* 本月销售/本月利润 */\r\n.salestoday {\r\n  width: 100%;\r\n  color: $blackColor;\r\n  overflow: hidden;\r\n  .list_title {\r\n    width: 100%;\r\n    height: 100px;\r\n    display: flex;\r\n    flex-wrap: nowrap;\r\n    justify-content: space-between;\r\n    margin: auto;\r\n    align-items: center;\r\n    // border-bottom: 1px solid $lightGrayColor;\r\n    background: linear-gradient(left, $blueColor, $skyBlueColor);\r\n    color: $whiteColor;\r\n    overflow: hidden;\r\n    padding: 0px 15px;\r\n    box-sizing: border-box;\r\n    font-size: 35px;\r\n    font-weight: 700;\r\n    .tixian_select {\r\n      width: 28%;\r\n      height: 70px;\r\n      color: $whiteColor;\r\n      background: rgba($color: #000000, $alpha: 0.2);\r\n      border: none;\r\n      font-size: 35px;\r\n    }\r\n  }\r\n  .sell{\r\n    overflow: hidden;\r\n    overflow: auto;\r\n    height: 100%;\r\n  .sell_list {\r\n    width: 95%;\r\n    margin: auto auto 100px auto;\r\n    li {\r\n      width: 100%;\r\n      height: 100px;\r\n      display: flex;\r\n      flex-wrap: nowrap;\r\n      overflow: hidden;\r\n      align-items: center;\r\n      border-bottom: 1px solid $lightGrayColor;\r\n      justify-content: space-between;\r\n      .date {\r\n        width: 27%;\r\n        overflow: hidden;\r\n      }\r\n      .amount {\r\n        width: 55%;\r\n        text-align: center;\r\n        overflow: hidden;\r\n        color: $orangeColor;\r\n      }\r\n      .detailed {\r\n        color: $blueColor;\r\n      }\r\n    }\r\n  }\r\n}\r\n}\r\n/* 库存管理 */\r\n.stock {\r\n  width: 100%;\r\n  overflow: hidden;\r\n  .head-title {\r\n    width: 100%;\r\n    height: 100px;\r\n    margin: auto;\r\n    display: flex;\r\n    flex-wrap: nowrap;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    color: $whiteColor;\r\n    font-size: 35px;\r\n    font-weight: 700;\r\n    // border-bottom: 1px solid $lightGrayColor;\r\n    background: linear-gradient(left, $bl